<template>
    <div class="h-full transition-all w-80 border-r border-gray-200 dark:bg-gray-700" :class="'story--fields--'+ randomId">
        <Fields :fields="fields" :data="settings" :freeform="true" />
    </div>
</template>
  
<script lang="ts" setup>
import { reactive, provide , useAttrs} from 'vue';
// Generate random id
const randomId = Math.random().toString(36).substr(2, 9);
var attrs = useAttrs();
attrs = attrs ?? {};
console.log(attrs);
const props = withDefaults(defineProps<{
    /**
     * The label of the button
     */
    name: String,
    label: String,
    type: String,
    options?: Object,
    description?: String,
    value?:String | Object



}>(), {
        type: 'text',
        label: 'Text',
        value: '',
        name: 'text',
        description: ''
});

provide(/* key */ 'teleport_id', /* value */ '.story--fields--'+ randomId) 

const fields = reactive(
    [{
        params: [{...attrs, ...props}]
    }]
);
var settings = reactive({
    text: props.value
});
if(props.value)
    settings[props.name] = props.value;



</script>